___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: #0d64b6;
    --primary-color-darker: #0a4b88;

    --font-comic: 'Comic Neue';
    --font-open: 'Open Sans';
}

* {
    box-sizing: border-box;
    outline: 0;
}

body {
    background: var(--primary-color);
    margin: 0;
    padding: 0;
    font-family: var(--font-open), sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;

    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
}

form {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: start;
}

form p{
    width: 100%;
}

form #num-input{
    font-size: 24px;
    height: 30px;
    width: 70%;
    padding: 0 15px;
    margin: 15px 0px;
}

form .converter-type {
    height: 20px;
    width: 30px;
}

form input:focus {
    outline: 1px solid var(--primary-color);
}

form button {
    border: none;
    background: var(--primary-color);
    color: white;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    width: 200px;
    cursor: pointer;
    margin-top: 20px;
}

form button:hover{
    background: var(--primary-color-darker);
}

.resultado {
    width: 90%;
    text-align: start;
}
